.popup {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  background: rgba(0, 0, 0, 0.5);
  z-index: 2;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  color: #000;
  visibility: hidden;
  opacity: 0;
  transition: visibility .1s, opacity .1s linear;
}