.header {
  max-width: 880px;
  margin: 45px auto 0;
  border-bottom: 1px solid rgba(84,84,84, 0.7);
  min-height: 74px;
  display: flex;
  justify-content: space-between;
}

@media (max-width: 950px) {
  .header {
    padding: 0 27px;
  }
}

@media (max-width: 425px) {
  .header {
    margin-top: 28px;
    min-height: 56px;
  }
}